.card {
    margin: 10px;
    border-radius: 5px;
    box-shadow: 0 4px 8px 0 rgb(175, 161, 161);
    background: #ffffff;
    width: 300px;
    min-height: 715px;
    margin: auto;
    text-align: center;
    font-family: arial;
    
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    vertical-align: middle;
  }

  .card h1 {
    min-height: 123px;
  }

  .card p{
    min-height: 20px;
    max-height: 120px;
  }
  
  .card img {
    padding-top: 10px;
    padding-bottom: 30px;
    min-height: 250px;
    max-height: 250px;
    max-width: 300px;
  }
  
  .card .price {
    color: grey;
    font-size: 22px;
    padding: 10px;
  }

  .card .desc {
    min-height: 120px;
  }
  
  .card button {
    border: none;
    outline: 0;
    padding-top: 12px;
    color: rgb(228, 4, 4);
    background-color: #000;
    text-align: center;
    cursor: pointer;
    width: 100%;
    font-size: 18px;
  }
  
  .card button:hover {
    opacity: 0.7;
  }